@font-face {
  font-family: "Aquire";
  src: url("Aquire-BW0ox.otf");
}

.Header {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: grey;
  border-bottom: 1px solid black;
  box-shadow: 0px 5px 10px 2px #383126;
}

.Header > p {
  font-family: "Aquire";
  color: #ec9200;
}

.Header button {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: none;
  outline: none;
  margin-left: 5px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Header > button > img {
  height: 20px;
  width: 20px;
}

.Search {
  position: relative;
  margin-left: 10px;
}

.Search input {
  width: 40em;
  height: 30px;
  border-radius: 5px;
  border: none;
  outline: none;
}

.Main {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  background-color: #ec9200;
  box-shadow: 0 0 10px 2px brown;
}

@media (max-width: 500px) {
  .Search input {
    width: 15em;
  }

  .Main {
    display: block;
  }
}
